<template>
  <div class="plan-list">
    <div class="search-box">
      <div class="search">
        <el-input
          v-model="listQuery.name"
          clearable
          placeholder="请输入计划名称/制作人"
          size="small"
          style="width:220px;"
          @keyup.native="inputChange"
        />
        <el-button
          type="primary"
          class="search-box-button"
          size="small"
          @click="searchListData"
          >查询</el-button
        >
      </div>
      <el-button
        type="primary"
        class="search-box-button"
        size="small"
        @click="addPlan"
        >添加计划</el-button
      >
    </div>
    <div class="table-content">
      <div class="table-content">
        <div slot="header" class="title">计划列表</div>
        <el-table :data="listData" style="width: 100%">
          <el-table-column
            align="center"
            prop=""
            label="序号"
            fixed
            min-width="60"
          >
            <template slot-scope="scope">
              {{ scope.$index + 1 }}
            </template>
          </el-table-column>
          <el-table-column align="center" prop="_id" label="编号" width="280" />
          <el-table-column
            align="center"
            prop="name"
            label="计划名称"
            min-width="120"
          />
          <el-table-column
            align="center"
            prop="make_name"
            label="制作人"
            min-width="120"
          />
          <el-table-column
            align="center"
            prop="created_at"
            label="时间"
            min-width="130"
          >
            <template slot-scope="scope">
              {{ scope.row.created_at | formatTime('{y}-{m}-{d}') }}
            </template>
          </el-table-column>
          <el-table-column align="center" label="状态" min-width="100">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.switchVal"
                @click.native="openStatus(scope.row)"
              />
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            prop="use_count"
            label="使用人数"
            min-width="90"
          />
          <el-table-column
            label="操作"
            align="center"
            fixed="right"
            min-width="200"
          >
            <template slot-scope="scope">
              <el-button size="small" type="text" @click="handleLook(scope.row)"
                >查看</el-button
              >
              <el-button size="small" type="text" @click="handleEdit(scope.row)"
                >编辑</el-button
              >
              <el-button
                size="small"
                type="text"
                @click="handleDelete(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
          <template slot="empty">
            <div class="table-empty">
              暂无数据
            </div>
          </template>
        </el-table>
        <div
          v-if="listData && listData.length > 0"
          class="learningAdmin-el-pagination"
        >
          <el-pagination
            :current-page.sync="listQuery.currentPage"
            :page-sizes="[12, 18, 24]"
            :page-size="listQuery.pageSize"
            layout="total,sizes, prev, pager, next"
            :total="tableCount"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          />
        </div>
      </div>
    </div></div
></template>
<script>
import '../common.scss'
import {
  getPlanList,
  deleSinglePlan,
  putSinglePlanStatus
} from '@/api/learningAdmin'
export default {
  data() {
    return {
      listData: null,
      listQuery: {
        name: '',
        pageSize: 12,
        currentPage: 1
      },
      tableCount: 0
    }
  },
  created() {
    this.getPlanListData(this.listQuery)
  },
  methods: {
    handleLook(row) {
      this.$router.push({
        path: `/learning-admin/PlanAdmin/looking`,
        query: {
          id: row._id,
          haddleType: '查看'
        }
      })
    },
    handleEdit(row) {
      this.$router.push({
        path: `/learning-admin/PlanAdmin/edit`,
        query: {
          id: row._id,
          haddleType: '编辑'
        }
      })
    },
    searchListData() {
      this.listQuery.currentPage = 1
      this.getPlanListData(this.listQuery)
    },
    handleSizeChange(e) {
      this.listQuery.pageSize = e
      this.getPlanListData(this.listQuery)
    },
    handleCurrentChange(e) {
      this.listQuery.currentPage = e
      this.getPlanListData(this.listQuery)
    },
    async getPlanListData(listQuery) {
      const list = await getPlanList(listQuery)
      this.listData = list.data.list.map(item => {
        if (item.status === 'disable') {
          item.switchVal = false
        } else {
          item.switchVal = true
        }
        return item
      })
      this.tableCount = list.data.count
    },
    async openStatus(row) {
      const data = {
        id: row._id
      }
      const resStatus = await putSinglePlanStatus(data)
      if (resStatus.code === 20000) {
        this.getPlanListData(this.listQuery)
      }
    },
    addPlan() {
      this.$router.push({
        path: `/learning-admin/PlanAdmin/add`,
        query: { haddleType: '添加' }
      })
    },
    async handleDelete(row) {
      const data = {
        id: row._id
      }
      if (row.use_count > 0) {
        this.$confirm('该计划正在被使用,不可以删除', '删除提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          center: true
        })
          .then(async () => {})
          .catch(() => {})
      } else {
        this.$confirm(
          '删除后，该计划信息将不再保留,请确定是否删除',
          '删除提示',
          {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            center: true
          }
        )
          .then(async () => {
            const resDelete = await deleSinglePlan(data)
            if (resDelete.data.result) {
              this.getPlanListData()
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
            } else {
              this.$confirm(resDelete.data.message, '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                center: true
              })
            }
          })
          .catch(() => {})
      }
    },
    inputChange(e) {
      setTimeout(() => {
        if (this.listQuery.name === '') {
          this.getPlanListData()
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.table-content {
  margin-top: 30px;
  padding: 20px;
  border: 1px solid #f5f7fa;
  .empty-text {
    text-align: center;
  }
  .table-empty {
    text-align: center;
  }
}
</style>
